@font-face {
  font-family: "Roboto";
  src: url("/src/fonts/Roboto-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("/src/fonts/Roboto-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
  color: hsl(0deg, 0%, 7%);
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 24px;
  -webkit-tap-highlight-color: hsl(0deg, 0%, 0%);
  -webkit-tap-highlight-color: transparent;
}

img {
  display: block;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

button {
  border: 1px solid transparent;
  cursor: pointer;
  transition: 0.3s;
  background: none;
}
button.primary-btn {
  font-weight: 700;
  line-height: 16px;
  color: hsl(0deg, 0%, 100%);
  background: hsl(21deg, 50%, 79%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 40px;
  border-radius: 32px;
}
button.primary-btn:hover {
  background: transparent;
  color: hsl(21deg, 50%, 79%);
  border-color: hsl(21deg, 50%, 79%);
}

.wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

.paragraph {
  font: 700 42px/50px "Roboto", sans-serif;
  margin-bottom: 24px;
}

.text {
  font-weight: 300;
}

table,
td,
th {
  border-collapse: collapse;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 10px;
  border: 1px solid hsl(0deg, 0%, 7%);
}

.loader {
  display: flex;
  animation: animate 1.5s linear infinite;
  clip: rect(0, 80px, 80px, 40px);
  height: 80px;
  width: 80px;
  position: absolute;
  left: calc(50% - 40px);
  top: calc(50% - 40px);
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(220deg);
  }
}
.loader:after {
  animation: animate2 1.5s ease-in-out infinite;
  clip: rect(0, 80px, 80px, 40px);
  content: "";
  border-radius: 50%;
  height: 80px;
  width: 80px;
  position: absolute;
}

@keyframes animate2 {
  0% {
    box-shadow: inset hsl(21deg, 50%, 79%) 0 0 0 17px;
    transform: rotate(-140deg);
  }
  50% {
    box-shadow: inset hsl(21deg, 50%, 79%) 0 0 0 2px;
  }
  100% {
    box-shadow: inset hsl(21deg, 50%, 79%) 0 0 0 17px;
    transform: rotate(140deg);
  }
}
.first-section {
  margin-top: 96px;
}
.first-section .box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 95px;
}
.first-section .box img {
  width: 100%;
  max-width: 505px;
  height: 330px;
  object-fit: cover;
}
.first-section .box .text-box {
  max-width: 380px;
}
.first-section .box .text-box p {
  margin-bottom: 32px;
}

.window-background {
  display: none;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: hsla(0deg, 0%, 7%, 0.5);
  z-index: 10;
}
.window-background .window-body {
  width: 100%;
  max-width: 920px;
  background: hsl(0deg, 0%, 100%);
  padding: 56px 95px;
  position: relative;
  max-height: 500px;
  overflow-y: scroll;
}
.window-background .window-body .window-title {
  margin-bottom: 24px;
}
.window-background .window-body .window-title button {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 40px;
  right: 40px;
}
.window-background .window-body .window-title button svg path {
  transition: 0.3s;
}
.window-background .window-body .window-title button:hover svg path {
  stroke: hsl(21deg, 50%, 79%);
}
.window-background .window-body .window-content p {
  font-weight: 300;
}
.window-background .window-body .window-content p span {
  font-weight: 700;
}
.window-background .window-body .window-content button {
  display: none;
  margin: 24px 0;
}
.window-background .window-body .window-content .additional-task {
  margin-top: 24px;
}

@media screen and (max-width: 1224px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
  .window-background {
    padding: 50px 20px;
  }
}
@media screen and (max-width: 1019px) {
  .first-section .box img {
    margin-bottom: 15px;
  }
  .first-section .box .text-box {
    max-width: 505px;
  }
}
@media screen and (max-width: 578px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
  .paragraph {
    font: 700 25px/30px "Roboto", sans-serif;
    margin-bottom: 15px;
  }
  .first-section .box .text-box p {
    margin-bottom: 25px;
  }
  .window-background {
    padding: 50px 10px;
  }
  .window-background .window-body {
    padding: 25px;
  }
  .window-background .window-body .window-title {
    margin-bottom: 15px;
  }
  .window-background .window-body .window-title h2 {
    margin-bottom: 0;
  }
  .window-background .window-body .window-title button {
    top: 20px;
    right: 20px;
  }
  .window-background .window-body .window-content button {
    margin-top: 15px 0;
  }
}